import React, {useState, useEffect} from 'react';
import { Card, Navbar, Form, FormControl } from 'react-bootstrap';
import {PullToRefresh, PullDownContent, ReleaseContent, RefreshContent} from "react-js-pull-to-refresh";

import './App.css';
import api from './api';

function App() {
  const [acoes, setAcoes] = useState([]);
  const [t, setT] = useState([]);

  const test = (value) => setT(acoes.filter( item => { return item.nome.includes(value)}))

  const loadAcoes = async  () => {
    
    const response = await api.get('/acoes/');

    const { acoes } = response.data

    setAcoes(acoes)
    setT(acoes)
  }
  useEffect(() => loadAcoes(), []);

  return (
    <PullToRefresh
      pullDownContent={<PullDownContent />}
      releaseContent={<ReleaseContent />}
      refreshContent={<RefreshContent />}
      pullDownThreshold={200}
      onRefresh={loadAcoes}
      triggerHeight={50}
      backgroundColor='white'
      startInvisible={true}
    >
      <div className="App">
        <Navbar bg="dark" variant="dark">
          <Navbar.Brand href="#home">
            {' Cotações Hoje'}
          </Navbar.Brand>
          <Form inline>
            <FormControl 
            type="text" 
            placeholder="Search" 
            className="mr-sm-2" 
            onChange={e => test(e.target.value.toUpperCase())}/>
          </Form>
         </Navbar>
         {t.length > 0 ? (
           <>
           {t.map( ({ _id, nome, ultima, data, variacao}) => (
             <Card key={_id} className="text-center"> 
             <Card.Body>{nome}  R${ultima}  {data} 
             <span className={`${variacao.includes('-') ? 'negativo' : 'positivo'}`} > {variacao} </span>
             </Card.Body>
           </Card>
           ))}
           </>

         ) : (<> </>)}

       </div>
    </PullToRefresh>
   
  );
}


export default App;
